.logo {
  cursor: pointer;
  position: fixed;
  width: 150px;
  z-index: 2;
}

.logo_text {
  stroke-dashoffset: 0;
}

.logo_text {
  opacity: 1;
  stroke-width: 150px;
  stroke: rgb(105, 55, 255);
}

.logo_icon {
    display: none;
}



.hero-container {
  min-width: 100vw;
  min-height: 100vh;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 90px;
}

.content {
  color: #fff;
  padding: 3rem 1rem;
}

.hi-text {
  color: rgb(105, 55, 255);
  /* color: rgb(100, 255, 218); */
  font-weight: 600;
  font-size: 1.3rem;                               
  padding: 5px;
  margin-bottom: 1rem;
}

.hero-title {
  font-size: clamp(40px, 3vw, 80px);
  color: rgb(204, 214, 246);
  font-weight: bold;
  overflow: hidden;
  padding: 5px;
  margin-bottom: 0.5rem;
}

.another-title {
  font-size: clamp(40px, 3vw, 80px);
    color: rgb(136, 146, 176);
    margin-bottom: 1rem;
}

.hero-title-text {
  overflow: hidden;
}

.hero-sub_title {
  font-style: italic;
  font-size: 1.8rem;
  padding: 0.5rem 0;
}

.letter {
    opacity: 0;
}

.hero-content-container {
    opacity: 0;
    padding: 5px;
    margin-top: 40px;
}

.hero-about-text {
    max-width: 40ch;
    padding: 1rem 0rem;
    margin-bottom: 1.5rem;
    line-height: 1.8;
    color: rgb(136, 146, 176);
}


.interactive-background-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  color: #fff;
  overflow: hidden;
  background-color: #1c1d25;
  z-index: -50;
  overflow: hidden;
}

.button-outer-container {
    display: inline-block;
    padding: 1rem 1rem 1rem 0rem;
    overflow-y: hidden;
}

.button-container {
    padding: 1rem 0.3rem 1rem 1rem;
    font-weight: 600;
    cursor: pointer;
    /* background-color: #f44330; */
    background-color: rgb(105, 55, 255);
    border-radius: 5px;
    align-items: center;
    display: inline-flex;
    position: relative;
    transition: all 0.5s;
}

.button-image-container {
    padding: 0.2rem;
    color: white;
    margin-left: 0.5rem;
    display: inline-flex;
    align-items: center;
    overflow: hidden;
    width: 30px;
}

.button-backlay {
    position: absolute;
    top: 30%;
    left: 5px;
    background-color: transparent;
    height: calc(100% - 30% - 1rem + 5px);
    width: calc(100% - 17px);
    z-index: -5;
    border: 1px solid rgb(105, 55, 255);
    border-radius: 5px;
    margin-right: 50px;
    transition: all 0.5s;
}

.button-outer-container:hover .button-backlay {
    -webkit-transform: scale(0.5) translate(-50%, -50%);
            transform: scale(0.5) translate(-50%, -50%);
}

.button-text {
    padding: 0.3rem 0rem; 
}

.button-outer-container:hover .button-container {
    /* background-color: #f44330; */
    background-color: rgb(255, 149, 215);
}



* {
  font-family: 'Spartan', sans-serif !important;
  position: relative;
  overflow-x: hidden;
}

.container {
    overflow: hidden;
}


.topbar-container {
  background-color: #1c1d25;
  position: fixed;
  top: 0;
  right: 0;
  left:0;
  height: 90px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: white;
}

@media only screen and (max-width: 750px) {
  .topbar-container > .link-container {
    display: none;
  }
}


.link-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 1.5rem;
  font-weight: 600;
  transition: color 0.5s;
  cursor: pointer;
  position: relative;
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s;
  height: 100%;
}

@-webkit-keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.link-underline {
  height: 0px;
  width: 100%;
  margin-top: 2px;
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
}

.link-text {
    padding: 0.3rem 0rem;
}



.close-icon-container {
    cursor: pointer;
}

.close-icon-line {
    opacity: 0;
    stroke: rgb(176, 178, 195);
    stroke-width: 4px;
    transition: stroke 0.25s ease-in-out;
}

.close-icon-container:hover .close-icon-line {
  stroke: #fff;
}




.navModal-container {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1000;
    background: linear-gradient(to right, #101c3c, #2359a4);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.navModal-underlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1000;
    background-color: #ef463c;
}

.close-icon-container {
    position: absolute;
    top: 30px;
    right: 25px;
}

.navModal-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}

.navModal-links > * {
    margin-top: 1.5rem;
}

.navModal-social-links {
    width: 100%;
    position: absolute;
    bottom: 20px;
    text-align: center;
    color: white;
    padding: 1rem;
    overflow: hidden;
}

.navModal-social-link-svg  {
    margin-right: 1rem;
    width: 30px;
    height: 30px;
    fill: white;
    cursor: pointer;
}

.burger-icon {
  margin-right: 1rem;
  margin-top: -5px;
  cursor: pointer;
  opacity: 0;
}

.burger-icon-line {
  stroke: rgb(176, 178, 195);
  stroke-width: 2px;
  transition: stroke 0.25s ease-in-out;
}

@media only screen and (min-width: 750px) {
  .burger-icon {
    display: none;
  }
}

.burger-icon:hover .burger-icon-line {
  stroke: #fff;
}

.social-icons-container {
    position: fixed;
    left: 20px;
    bottom: 0px;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 750px) {
    .social-icons-container {
        display: none;
    }
}

.social-icon {
    padding: 0.5rem;
}

.social-icon-svg {
    height: 25px;
    width: 25px;
    fill: none;
    stroke: rgb(168, 178, 209);
    stroke-width: 1;
    cursor: pointer;
    transition: all 0.25s;
}

.social-icon:hover .social-icon-svg {
    stroke: #6937ff;
    -webkit-transform: translateY(-5px) scale(1.1);
            transform: translateY(-5px) scale(1.1);
}

.social-icon-line {
    margin-top: 2rem;
    height: 6rem;
    width: 1px;
    background-color: rgb(168, 178, 209);
}




/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

